/* 底层导航栏 */
.footerNav{
    width: 100vw;
    height: .6rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #fff;
    border-top: 1px solid #d9d9d9;
    position: fixed;
    bottom: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.footerNav a{
    text-align: center;
}
.footerNav a span{
    display: block;
    font-size: .06rem;
}
.footerNav a span.iconfont{
    font-size: .12rem;
}
.footerNav a.active{
    color: var(--c1);
}
.router-link-active{
    color: var(--c1);
}


